:root {
  --yellow: #fecb2e;
  --white: #ffffff;
  --light-blue: #5fc9f8;
  --blue: #147efb;
  --shadow: rgba(0, 0, 0, 0.3); }

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
  color: #212529; }

.body {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 200px; }

a {
  text-decoration: none; }

.body li {
  list-style-type: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden; }

@charset "UTF-8";
.widget {
  position: relative;
  width: 600px; }
  .widget__list--show .widget__item {
    display: block; }
  .widget__item {
    display: none;
    box-shadow: 0 4px 12px var(--shadow);
    border-radius: 16px;
    margin-bottom: 20px; }
    .widget__item:first-child {
      display: block; }
  .widget__content {
    margin-bottom: 10px; }
    .widget__content-container {
      position: relative;
      top: -16px; }
  .widget__photo {
    height: 300px;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 16px 16px 0 0; }
  .widget__title {
    margin-bottom: 20px;
    padding: 20px 40px;
    font-size: 20px;
    color: var(--white);
    background-color: var(--light-blue);
    border-radius: 16px 16px 0 0; }
  .widget__author {
    font-size: 14px;
    padding: 0 40px; }
  .widget__date {
    display: flex;
    justify-content: flex-end;
    font-size: 14px;
    padding: 0 40px 20px; }
  .widget__header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
    padding: 0 40px 0 20px; }
  .widget__link {
    font-size: 14px;
    padding: 10px 20px;
    border-radius: 16px;
    transition: .3s; }
    .widget__link:hover {
      color: var(--white);
      background-color: var(--blue);
      outline: none; }
    .widget__link:focus-within {
      color: var(--white);
      background-color: var(--blue);
      outline: none; }
    .widget__link:active {
      opacity: .6; }
  .widget__status::before {
    content: '';
    opacity: .6;
    font-size: 14px; }
  .widget__status--not-read::before {
    content: 'Не прочитано'; }
  .widget__status--read::before {
    content: 'Прочитано'; }
  .widget__btn {
    width: 100%;
    padding: 10px 0;
    border: none;
    border-radius: 16px;
    background-color: var(--white);
    box-shadow: 0 4px 12px var(--shadow);
    font-weight: bold;
    cursor: pointer;
    outline: none;
    transition: .3s; }
    .widget__btn:hover {
      background-color: var(--yellow); }
    .widget__btn:focus-within {
      background-color: var(--yellow); }
    .widget__btn:active {
      opacity: .6; }
